  background: #fff;
  padding-bottom: 10px;
  margin-bottom: 0;
  border-bottom: none;
}
.mode-switcher-panel .panel-group .panel-title .title::after {
  position: absolute;
  content: "";
  left: 0;
  top: 10px;
  height: 1px;
  width: 100%;
  background: #ebebeb;
  z-index: -1;
}
.mode-switcher-panel .panel-group .color-skin {
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  margin: -7px -7px 0px;
}
.mode-switcher-panel .panel-group .color-skin .color-item {
  display: inline-block;
  position: relative;
  -webkit-box-flex: 1;
  flex: 1 0 calc(25% - 14px);
  margin: 7px;
  border-radius: 2px;
  cursor: pointer;
}
.mode-switcher-panel .panel-group .color-skin .color-item::before {
  content: "";
  display: block;
  padding-bottom: 100%;
}
.mode-switcher-panel .panel-group .color-skin .color-item::after {
  position: absolute;
  content: "";
  left: 50%;
  top: calc(50% - 5px);
  height: 7px;
  width: 12px;
  border: 2px solid #fff;
  border-top: none;
  border-right: none;
  opacity: 0;
  visibility: hidden;
  transform: translateX(-50%) rotate(-45deg);
}
.mode-switcher-panel .panel-group .color-skin .color-item.active::after {
  opacity: 1;
  visibility: visible;
}

