.mode-switcher-panel .switcher-minimize-button {
  position: absolute;
  font-size: 24px;
  top: 30px;
  border: 0;
  left: -45px;
  display: -webkit-box;
  display: flex;
  height: 50px;
  width: 50px;
  font-size: 20px;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  color: #0068e1;
  color: var(--color-primary);
  background: #fff;
  border-radius: 50% 0 0 50%;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.15);
  cursor: pointer;
  z-index: -1;
}

.mode-switcher-panel .switcher-minimize-button i {
  font-size: inherit;
  pointer-events: none;
  transition: 0.4s;
}

.mode-switcher-panel .switcher-minimize-button:focus {
  outline: none;
}

.position-fixed-right {
  position: fixed;
  right: 0;
  transform: translateY(-50%) translateX(100%);
  top: 50%;
  z-index: 99;
}

.theme-change-button {
  overflow: hidden;
  position: fixed;
  bottom: 24px;
  right: 24px;
  z-index: 9999;
}
.theme-change-button .toggle-button-switcher {
  border: 2px solid #fff;
  border-radius: 50px;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.theme-change-button .toggle-button {
  width: 24px;
  height: 24px;
  outline: none;
  -webkit-appearance: none;
  position: relative;
  transition: 0.4s;
  background: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 17.625C15.1066 17.625 17.625 15.1066 17.625 12C17.625 8.8934 15.1066 6.375 12 6.375C8.8934 6.375 6.375 8.8934 6.375 12C6.375 15.1066 8.8934 17.625 12 17.625Z' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M12 3.375V1.5' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M5.89683 5.89689L4.57495 4.57501' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M3.375 12H1.5' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M5.89683 18.1031L4.57495 19.425' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M12 20.625V22.5' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M18.1031 18.1031L19.425 19.425' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M20.625 12H22.5' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M18.1031 5.89689L19.425 4.57501' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E") no-repeat center;
