@use "../abstracts/mixins" as *;
@use "../bs-customization/colors" as *;
.d-btn {
  border-radius: 6px !important;
  font-size: 14px;
  font-weight: 700;
  display: inline-flex;
  text-transform: uppercase;
  padding: 1.1em 1.6em;
  line-height: 1;
  width: max-content;
  align-items: center;
  justify-content: center;
  transition: .3s;
  svg{
    margin-left: 12px;
  }
  &:focus{
    box-shadow: none;
    outline: none;
  }
  &:active{
    &:focus{
      box-shadow: none;
      outline: none;
    }
  }
  &.d-btn-primary{
    border: 2px solid var(--bs-primary-500);
    background: var(--bs-primary-500);
    color: white;
    svg{
      path{
        transition: .3s;
      }
    }
    &:hover{
      background: transparent;
      color: var(--bs-primary-500);
      svg{
        path{
          stroke: var(--bs-primary-500);
        }
      }
    }
  }
  &.d-btn-outline-primary{
    color: var(--bs-primary-500);
    border: 2px solid var(--bs-primary-50);
    box-sizing: border-box;
    border-radius: 8px;
    svg{
      margin-left: 12px;
    }
    &:hover{
        border: 2px solid var(--bs-primary-500);
    }
    svg{
      path{
        stroke: var(--bs-primary-500) !important;
      }
    }
}
}
button {
  &:focus {
    outline: none;
  }
}
.site-header{
  .header-btn{
    @media (max-width:991px) {
      text-align: center;
      margin-top: 55px;
    }
    .developer-header-btn{
      margin-left: 12px;
    }
  }
}
.d-video-link-btn{
  display: flex;
  align-items: center;
  font-weight: 600;
font-size: 16px;
color: white !important;
line-height: 24px;
.line{
  margin-right: 16px;
  width: 39px;
  height: 2px;
  background: rgba(255, 255, 255,0.24)
}
  svg{
margin-right: 12px;
  }
}
.d-video-btn{
  background: white;
  box-shadow: 0px 16px 48px rgba(0, 0, 0, 0.24);
  @include square(88px,500px);
  @include flexAllCenter;
}

.load-more-btn{
  border: 2px solid var(--bs-primary-50);
  display: inline-flex;
  line-height: 1;
  width: max-content;
  align-items: center;
  justify-content: center;
  transition: .3s;
  font-weight: 600;
  font-size: 16px;
  padding: 1.1em 1.6em;
  color: var(--bs-primary-500) !important;
  background: var(--bs-primary-50);
  border-radius: 8px;
  transition: .3s;
  svg{
    path{
      stroke: var(--bs-primary-500) !important;
    }
  }
  &:hover{
    background: transparent;
  }
}
